import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuController, NavController } from '@ionic/angular';
import { InsuranceAppService } from '../services/insurance-app.service';
@Component({
  selector: 'app-explore-screen-before-login',
  templateUrl: './explore-screen-before-login.page.html',
  styleUrls: ['./explore-screen-before-login.page.scss'],
})
export class ExploreScreenBeforeLoginPage implements OnInit {
  show = false;
  slideOpts = {
    initialSlide: 0,
    speed: 400
  };
  userID: any;
  constructor(public router: Router,
    public navCtrl: NavController,
    public menuCtrl: MenuController,
    public api: InsuranceAppService) { }

  ngOnInit() {
    this.userID = localStorage.getItem('userid');
    console.log('userid---', this.userID);

  }
  // ionViewDidEnter() {
  //   this.menuCtrl.enable(false);
  //   // this.RegisterForm.reset();
  // }
  // ionViewWillLeave() {
  //   // enable the root left menu when leaving this page
  //   this.menuCtrl.enable(true);
  // }

  insured() {
    if (this.userID != null) {
      this.navCtrl.navigateRoot('home-page-screen-after-login');

    } else {
      this.api.presenttoast('You are not loggedIn')
      this.navCtrl.navigateRoot('sign-in-screen');
    }

  }
  ReportaClaim() {
    if (this.userID != null) {
      this.navCtrl.navigateRoot('makeaclaim')
    } else {
      this.api.presenttoast('You are not loggedIn')
      this.navCtrl.navigateRoot('sign-in-screen');
    }

  }
  RenewPolicy() {
    if (this.userID != null) {
      this.navCtrl.navigateRoot('renewals')
    } else {
      this.api.presenttoast('You are not loggedIn')
      this.navCtrl.navigateRoot('sign-in-screen');
    }

  }
  verifyPolicy() {
    if (this.userID != null) {
      this.navCtrl.navigateRoot('verify-policy-screen-cust')
    } else {
      this.api.presenttoast('You are not loggedIn')
      this.navCtrl.navigateRoot('sign-in-screen');
    }

  }
  agentlogin() {
    if (this.userID != null) {
      this.navCtrl.navigateRoot('agentidpopup')
    } else {
      this.api.presenttoast('You are not loggedIn')
      this.navCtrl.navigateRoot('sign-in-screen');
    }

  }
  getintouch() {
    if (this.userID != null) {
      this.navCtrl.navigateRoot('contactus')
    } else {
      this.api.presenttoast('You are not loggedIn')
      this.navCtrl.navigateRoot('sign-in-screen');
    }

  }


  tab1Click() {
    this.navCtrl.navigateRoot('sign-up-screen');
  }
  tab2Click() {
    this.navCtrl.navigateRoot('home-page-screen-after-login')
  }
  tab3Click() {
    this.navCtrl.navigateRoot('sign-in-screen');
  }
  toogleShow() {
    this.show = !this.show
  }
  signInScreen() {
    this.router.navigate(['/sign-in-screen']);
  }
  signUpScreen() {
    this.router.navigate(['/sign-up-screen']);
  }
}
